.work {
  width: 100%;
  height: 680px;
  background-color: #fafafa;
  h1 {
    text-align: center;
    margin-top: 0;
    font-family: 'Oxygen', sans-serif;
    font-size: 41px;
    padding-bottom: 30px;
  }
  .flex1 {
    width: 1120px;
    margin: 0 auto;
    background-color: #fafafa;
    display: flex;
    flex-direction: column;
    transition: width 1s;
    &:hover {
      width: 1220px;
    }
    .btns {
      display: flex;
      flex-direction: row;
      justify-content: space-between;
      a {
        margin-top: 60px;
        align-self: center;
        border-radius: 6px;
        box-shadow: 0 10px 16px 0 rgba(0, 0, 0, 0.24),
          0 17px 50px 0 rgba(0, 0, 0, 0.19);

        .btn {
          width: 232px;
          height: 45px;
          border-radius: 6px;
          background-color: #fafafa;
          cursor: pointer;
          font-size: 20px;
          font-family: 'Oxygen', sans-serif;
          transition: all 0.5s;
        }
        .btn span {
          cursor: pointer;
          display: inline-block;
          position: relative;
          transition: 0.5s;
        }
        .btn span:after {
          content: '\00bb';
          position: absolute;
          opacity: 0;
          top: 0;
          right: -10px;
          transition: 0.5s;
        }

        .btn:hover span {
          padding-right: 25px;
        }

        .btn:hover span:after {
          opacity: 1;
          right: 0;
        }
      }
    }
  }
}
